@tailwind base;
@tailwind components;
@tailwind utilities;

@import './styles/__variables';

/* Your existing CSS */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-content {
  padding: 100px 0 50px 0;
  min-height: calc(100vh - 81px);
  background: #f3f3f3;
}

.section-padding {
  padding: 60px 0;
}

.section-title {
  margin-bottom: 50px;
  text-align: center;
}

.section-title h2 {
  font-size: 32px;
  font-weight: bold;
  text-transform: uppercase;
  position: relative;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

.section-title h2::after {
  content: '';
  position: absolute;
  display: block;
  width: 50px;
  height: 3px;
  background: var(--primary);
  bottom: 0;
  left: calc(50% - 25px);
}

.justify-between {
  justify-content: space-between;
}

.white-bg {
  background-color: #fff;
}

.no-copy {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.container {
  // max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}


// src/components/Blog/BlogIndividual.scss
.post {
  // padding-top: 80px;
  margin-top: -39px;

