.poetry-block {
    display: inline-block;
    width: 100%;
  }
  
  .poetry-info {
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
    margin-bottom: 30px;
    transition: all 0.3s ease;
  }
  
  .poetry-info:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  .poetry-info img {
    width: 100%;
    height: 250px;
    object-fit: cover;
  }
  
  .poetry-txt {
    padding: 25px;
  }
  
  .poetry-txt h4 {
    font-size: 19px;
    margin-bottom: 15px;
    font-weight: 600;
  }
  
  .poetry-txt h4 a {
    color: #222;
    text-decoration: none;
    transition: all 0.3s ease;
  }
  
  .poetry-txt h4 a:hover {
    color: var(--primary);
  }
  
  .separator {
    color: #777;
    margin: 0;
  }
  
  @media (max-width: 768px) {
    #poetry {
      padding-top: 60px;
      padding-bottom: 60px;
    }
  
    .poetry-info img {
      height: 200px;
    }
  
    .poetry-txt h4 {
      font-size: 17px;
    }
  }